$color-white: white;
$color-grey: #b9b9b9;
$color-pink: #ff597a;
$color-background: #fff0f0;
$text-color: #4d4d4d;
$footer-color: #dfdfdf;
$color-green: #8DF073;
$color-blue: #738BF0;

$radius: 20pt;
$radius-small: 5pt;
$line-main: 3pt solid $color-pink;

/* Importing Google fonts - Inter  */
@import url('https://fonts.googleapis.com/css2?family=Inter:opsz,wght@14..32,100..900&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Yesteryear&display=swap');

@mixin respond($breakpoint) {
    @if $breakpoint == phone {
        @media(max-width: 600px) {
            @content; } }
    @if $breakpoint == tablet {
        @media(max-width: 1024px) {
            @content; } }
    @if $breakpoint == desctop {
        @media(max-width: 1200px) {
            @content; } }
    @if $breakpoint == bigdesctop {
        @media(max-width: 1600px) {
            @content; } } }


@mixin form-style($color, $text-color, $border-color) {
    padding: 2rem;
    background-color: $color;
    border-radius: 1rem;
    h2 {
        text-align: left;
        @include respond(desctop) {
            text-align: left; }
        @include respond(tablet) {
            text-align: center; }
        @include respond(phone) {
            text-align: center; }
        color: $color-white;
        margin-bottom: 0.5rem; }
    hr {
        width: 50%;
        @include respond(desctop) {
            width: 70%; }
        @include respond(tablet) {
            width: 100%; }
        @include respond(phone) {
            width: 100%; }
        border: 2pt solid $color-pink; }
    p {
        color: $text-color;
        label {
            display: block;
            margin-bottom: 2rem; }
        input, select, textarea {
            font-size: 2rem;
            border: .1rem solid $color-pink;
            padding: 1rem;
            border-radius: .5rem; }
        input, textarea {
            width: 100%; }
        select {
            width: 30%;
            margin-right: 2rem; } }
    button {
        display: block;
        padding: 0.5rem 1.5rem;
        border-radius: $radius;
        background-color: $color-pink;
        color: $color-white;
        cursor: pointer;
        font-size: 1.6rem;
        font-weight: 600;
        text-align: center; } }


