.rotate {
    aspect-ratio: 1 / 1.3;
    @include respond(bigdesctop) {
        aspect-ratio: 1 / 1.0; }
    @include respond(desctop) {
        aspect-ratio: 1 / 1.1; }
    @include respond(tablet) {
        aspect-ratio: 1 / 1.0; }
    @include respond(phone) {
        aspect-ratio: 1 / 1.6; }
    width: 30%;
    @include respond(bigdesctop) {
        width: 45%; }
    @include respond(desctop) {
        width: 48%; }
    @include respond(tablet) {
        width: 90%; }
    @include respond(phone) {
        width: 99%; }
    margin-bottom: 35pt;
    border-radius: $radius;
    text-align: center;
    perspective: 1500pt;
    position: relative;
    cursor: pointer;
    &__card {
        width: 100%;
        height: 100%;
        transition: 1s all ease;
        backface-visibility: hidden;
        position: absolute;
        top: 0;
        left: 0;
        &_front {
            border-radius: $radius; }
        &_back {
            text-align: left;
            vertical-align: middle;
            padding: 3% 5%;
            background-color: $color-white;
            border-radius: $radius;
            transform: rotateY(180deg);
            ul {
                list-style-type: none;
                line-height: 1.8rem;
                @media(max-width: 1700px) {
                    line-height: 1.6rem; }
                @media(max-width: 1600px) {
                    line-height: 1.8rem; }
                @media(max-width: 1380px) {
                    line-height: 1.6rem; }
                @media(max-width: 1315px) {
                    line-height: 1.4rem; }
                @media(max-width: 1200px) {
                    line-height: 1.5rem; }
                @media(max-width: 1120px) {
                    line-height: 1.4rem; }
                @media(max-width: 1100px) {
                    line-height: 1.3rem; }
                @media(max-width: 1024px) {
                    line-height: 1.8rem; }
                @media(max-width: 670px) {
                    line-height: 1.6rem; }
                @media(max-width: 640px) {
                    line-height: 1.4rem; }
                @media(max-width: 600px) {
                    line-height: 1.8rem; }
                @media(max-width: 475px) {
                    line-height: 1.6rem; }
                @media(max-width: 450px) {
                    line-height: 1.4rem; } } } }
    &__img {
        display: block;
        border-radius: $radius;
        width: 100%;
        height: 100%;
        object-fit: cover; }
    &__ico {
        display: inline-block;
        height: 80pt;
        animation: finger 2s infinite; }
    &__tap-me {
        width: 60pt;
        height: 60pt;
        border-radius: 40pt;
        border: solid 5pt $color-white;
        margin: auto;
        margin-bottom: -20pt;
        background-color: #ffffff00;
        animation: tapme 2s infinite; }
    &__text {
        position: absolute;
        bottom: 50pt;
        left: 0;
        right: 0;
        text-align: center;
        padding: 10pt;
        color: $color-white;
        text-shadow: 0 0 5pt black, 0 0 10pt black, 0 0 15pt black, 0 0 20pt pink; }
    h3 {
        font-size: 1.4rem;
        margin-bottom: 30pt;
        @include respond(desctop) {
            font-size: 1.2rem; }
        @include respond(tablet) {
            font-size: 1.2rem; }
        @include respond(phone) {
            font-size: 1.2rem; } } }

.rotated .rotate__card_front {
    transform: rotateY(180deg); }

.rotated .rotate__card_back {
    transform: rotateY(0deg); }

@keyframes tapme {
    35% {
        transform: scale(1.2); }
    70% {
        transform: scale(1.4); }
    100% {
        transform: scale(1.6); } }

@keyframes finger {
    0% {
        transform: rotateX(0deg); }
    100% {
        transform: rotateX(30deg) translateY(-30pt);
