.photo-with-text {
    display: flex;
    justify-content: left;
    align-items: center;
    flex-wrap: wrap;
    margin-bottom: 20pt;
    &__photo {
        width: 40%;
        @include respond(bigdesctop) {
            width: 40%; }
        @include respond(desctop) {
            width: 40%; }
        @include respond(tablet) {
            width: 50%; }
        @include respond(phone) {
            width: 100%; }
        img {
            width: 100%; } }
    &__text {
        padding: 20pt 30pt;
        width: 60%;
        @include respond(bigdesctop) {
            width: 60%; }
        @include respond(desctop) {
            width: 60%; }
        @include respond(tablet) {
            width: 50%; }
        @include respond(phone) {
            width: 100%; } } }

.section-feedback {
    padding: 5rem 5%;
    &__wrapper {
        display: flex;
        gap: 2%;
        justify-content: left;
        flex-wrap: wrap; }
    &__feedback {
        width: 49%;
        @include respond(desctop) {
            width: 100%; }
        position: relative;
        background-color: $color-white;
        padding: 3rem;
        margin-bottom: 3rem;
        border-radius: 1rem;
        border: .1rem solid $color-white;
        display: flex;
        gap: 3rem;
        justify-content: left;
        img {
            height: 5rem;
            width: 5rem; }
        & span {
            display: inline-block;
            font-size: 2.5rem;
            font-weight: 800;
            margin-right: 3rem;
            border-bottom: .2rem solid $color-pink; }
        p {
            a {
                &:link, &:visited {
                    color: $color-pink; } } } } }

.form-wrapper {
    @include form-style($text-color, $color-white, $color-white); }

footer {
    padding: 1% 3%;
    display: flex;
    background-color: $footer-color;
    @include respond(tablet) {
        display: block;
        text-align: center; }
    justify-content: space-around;
    align-items: center;
    flex-wrap: wrap;
    .footer__left {
        display: flex;
        justify-content: flex-start;
        @include respond(tablet) {
            display: block; } }
    .footer__right {
        display: flex;
        justify-content: flex-end;
        @include respond(tablet) {
            justify-content: center; } }
    img {
        height: 48pt;
        margin: 10pt;
        @include respond(desctop) {
            height: 48pt;
            margin: 10pt; }
        @include respond(tablet) {
            height: 40pt;
            margin: 7pt; }
        @include respond(phone) {
            height: 32pt;
            margin: 5pt; } } }

.button {
    width: fit-content;
    margin: 0 auto;
    margin-bottom: 30pt;
    padding: 8pt 16pt;
    border-radius: $radius;
    color: $color-white;
    background: $color-pink;
    // box-shadow: 0pt 0pt 10pt $color-grey
    // text-shadow: 0pt 0pt 0pt $color-white
    font-size: 1.5rem;
    font-weight: 600;
    cursor: pointer; }

.phone {
    display: none;
    @include respond(phone) {
        display: block; } }


/* Адаптивный слайдер для отзывов */

.gallery {
    display: flex;
    align-items: center;
    justify-content: center;
    align-items: stretch;
    background-color: $color-background;
    margin-bottom: 30pt; }

.card-wrapper {
    width: 90%;
    margin: 0 60px 35px;
    padding: 20px 10px;
    color: #ffffff00; }

.card-list .card-item {
    list-style: none; }

.card-list .card-item .card-link {
    display: block;
    padding: 0pt;
    user-select: none;
    border-radius: $radius;
    text-decoration: none;
    cursor: pointer;
    transition: 0.2s ease; }

.card-list .card-item .card-link:active {
    cursor: grabbing; }

.card-list .card-item .card-link:hover {
    border-color: $color-pink; }

.card-list .card-link .card-image {
    width: 100%;
    border-radius: $radius;
    aspect-ratio: 16 / 9;
    object-fit: cover; }

// .card-list .card-link .card-image:hover
//     box-shadow: 0pt 0pt 10pt $color-pink

.card-list .card-link .badge {
    color: #ffffff00;
    width: fit-content;
    border-top-right-radius: $radius;
    border-bottom-right-radius: $radius;
    margin: 4pt 0 4pt;
    position: absolute;
    top: 20pt;
    z-index: 2; }

.card-list .card-link .badge-tour {
    color: $color-white;
    background: $color-pink;
    padding: 8pt 16pt;
    font-size: 1.2rem;
    font-weight: 600; }

.card-wrapper .swiper-pagination-bullet {
    height: 13px;
    width: 13px;
    opacity: 0.5;
    background: $color-pink; }

.card-wrapper .swiper-pagination-bullet-active {
    opacity: 1; }

.card-wrapper .swiper-slide-button {
    color: $color-pink;
    margin-top: -35px; }

/* Responsive media query code for small screens */
@media (max-width: 768px) {
    .card-wrapper {
        margin: 0 10px 25px; }

    .card-wrapper .swiper-slide-button {} }


/* ТАЙМЕР */

.timer {
    &__items {
        display: flex;
        justify-content: center;
        gap: 1%;
        margin-bottom: 20pt;
        font-size: 7rem;
        @include respond(desctop) {
            font-size: 6rem; }
        @include respond(tablet) {
            font-size: 5rem; }
        @include respond(phone) {
            font-size: 4rem; } }
    &__item {
        color: $color-pink;
        font-family: "Yesteryear", cursive;
        font-weight: 400;
        font-style: normal;
        position: relative;
        min-width: 60px;
        margin-left: 10px;
        margin-right: 10px;
        padding-bottom: 0px;
        text-align: center; }
    &__item::before {
        content: attr(data-title);
        display: block;
        position: absolute;
        left: 50%;
        bottom: 0;
        transform: translateX(-50%);
        font-size: 14px; }
    &__item:not(:last-child)::after {
        content: ':';
        position: absolute;
        right: -30px; }
    &__title {
        margin-bottom: 0;
        color: $color-pink; } }


/* АККОРДИОН */

.accordion {
	background-color: $color-background;
	color: $text-color;
	cursor: pointer;
	padding: 18pt;
	width: 100%;
	text-align: left;
	font-size: 1.5rem;
	transition: 0.4s; }

.accordion__wrapper {
    border-top: 2pt solid $color-pink; }

.accordion__wrapper:last-child {
    border-bottom: 2pt solid $color-pink; }

.active, .accordion:hover {
	background-color: $color-background; }

.accordion:after {
	content: '\002B';
	color: $text-color;
	font-weight: bold;
	float: right;
	margin-left: 5px; }

.active:after {
	content: "\2212"; }

.panel {
	padding: 0 18pt;
	background-color: $color-background;
	max-height: 0;
	overflow: hidden;
	transition: max-height 0.2s ease-out; }


/* СЛАЙДЕР ДЛЯ ОПИСАНИЯ */

.shedule {
    margin: 0 3%;
    &__wrapper {
        width: 100%;
        position: relative;
        display: flex;
        flex-wrap: wrap;
        justify-content: space-around;
        margin-bottom: 3rem;
        background-color: $color-white;
        border-radius: 1rem; }
    &__photo {
        position: relative;
        width: 40%;
        overflow: hidden;
        @include respond(desctop) {
            width: 50%; }
        @include respond(tablet) {
            width: 100%; }
        @include respond(phone) {
            width: 100%; }
        img {
            position: absolute;
            top: 0;
            left: 0;
            min-width: 100%;
            width: auto;
            min-height: 40rem;
            height: 100%;
            @media(max-width: 1650px) {
                min-height: 37rem; }
            @media(max-width: 1500px) {
                min-height: 35rem; }
            @media(max-width: 1350px) {
                min-height: 33rem; }
            @media(max-width: 1200px) {
                min-height: 40rem; }
            @media(max-width: 1050px) {
                min-height: 37rem; }
            @media(max-width: 900px) {
                position: relative;
                width: 100%;
                height: auto; }
            @media(max-width: 600px) {
                height: auto; }
            // @media(max-width: 450px)
            //     min-height: 40rem
            // @media(max-width: 420px)
            //     min-height: 38rem
            // @media(max-width: 380px)
 } }            //     min-height: 36rem
    &__text {
        flex: 1;
        width: 40%;
        @include respond(desctop) {
            width: 50%; }
        @include respond(tablet) {
            width: 100%; }
        @include respond(phone) {
            width: 100%; }
        min-height: 40rem;
        @media(max-width: 1650px) {
            min-height: 37rem; }
        @media(max-width: 1500px) {
            min-height: 35rem; }
        @media(max-width: 1350px) {
            min-height: 33rem; }
        @media(max-width: 1200px) {
            min-height: 40rem; }
        @media(max-width: 1050px) {
            min-height: 37rem; }
        @media(max-width: 600px) {
            min-height: 42rem; }
        padding: 3rem 3%;
        li {
            list-style-type: none; } }
    .prev, .next {
        cursor: pointer;
        position: absolute;
        transform: translate(-0%, -50%);
        top: 50%;
        width: auto;
        padding: 25% 16px;
        color: white;
        font-weight: bold;
        font-size: 18px;
        transition: 0.6s ease; }
    .next {
        right: 0; }
    .prev:hover, .next:hover {
        background-color: rgba(0, 0, 0, 0.8); }
    &__photo-text {
        color: $color-white;
        text-shadow: (0 0 3rem rgba($color-grey, 1));
        font-size: 20px;
        font-weight: 600;
        padding: 8px 12px;
        position: absolute;
        bottom: 40px;
        left: 50%;
        transform: translateX(-50%); }
    .dots {
        position: absolute;
        bottom: 20px;
        left: 50%;
        transform: translateX(-50%); }
    .dot {
        cursor: pointer;
        height: 13px;
        width: 13px;
        margin: 0 2px;
        background-color: $color-white;
        border-radius: 50%;
        display: inline-block;
        transition: background-color 0.6s ease; }
    .active, .dot:hover {
        background-color: $color-pink; } }


/* СЛАЙДЕР НА ГЛАВНОМ ЭКРАНЕ */

.slideshow {
    width: 100%;
    height: 70vh;
    position: relative;
    overflow: hidden;
    background: #000;
    margin: 20px 0; }

.slideshow-item {
    width: 100%;
    height: 100%;
    position: absolute;
    opacity: 0;
    animation: slideanim 20s infinite;
    pointer-events: none; }

.slideshow-item:nth-child(1),
.slideshow-item:nth-child(1) img {
    animation-delay: 0; }

.slideshow-item:nth-child(2),
.slideshow-item:nth-child(2) img {
    animation-delay: 5s; }

.slideshow-item:nth-child(3),
.slideshow-item:nth-child(3) img {
    animation-delay: 10s; }

.slideshow-item:nth-child(4),
.slideshow-item:nth-child(4) img {
    animation-delay: 15s; }

.slideshow-item img {
    width: 100%;
    height: 100%;
    object-fit: cover;
    animation: zoom 20s infinite; }

.slideshow-item-text {
    width: 100%;
    text-align: center;
    position: absolute;
    bottom: 50%;
    left: 50%;
    transform: translate(-50%, 50%);
    color: $color-white; }

.slideshow-item-text h1 {
    color: $color-white;
    text-shadow: 0 0 5pt black, 0 0 10pt black, 0 0 15pt black, 0 0 20pt black;
    font-size: 3rem;
    @include respond(desctop) {
        font-size: 3rem; }
    @include respond(tablet) {
        font-size: 2rem; }
    @include respond(phone) {
        font-size: 1.5rem; } }

// .slideshow-item-text p
//     font-size: 15px
//     margin-bottom: 10px

@keyframes slideanim {
    12.5% {
        opacity: 1;
        pointer-events: auto; }
    25% {
        opacity: 1;
        pointer-events: auto; }
    37.5% {
        opacity: 0; } }

@keyframes zoom {
    50% {
        transform: scale(1.3); } }

@media screen and (max-width: 1100px) {
    .slideshow-item-text {
        max-width: 100%; } }

@media screen and (max-width: 456px) {
    .slideshow-item-text {
        bottom: 0;
        left: 0;
        max-width: 100%; }
    .slideshow-item-text h1 {
        font-size: 18px; }
    .slideshow-item-text p {
        font-size: 13px; } }
